import './App.css';
import '@mantine/core/styles.css';
import { MantineProvider, SimpleGrid, Select, Container, Group, Checkbox, Modal, Button, ActionIcon, AppShell } from '@mantine/core';
import { TimeInput } from '@mantine/dates';
import { use, useEffect, useState } from 'react';
import axios from 'axios';
import { DataTable } from 'mantine-datatable';
import 'mantine-datatable/styles.layer.css';



export function App() {

  if (useState === undefined) {
    throw new Error('React.useState is not defined. Please ensure you are using a compatible version of React.');
  }

  const [lessons, setLessons] = useState([]);
  const [tracks, setTracks] = useState([]);
  const [libraries, setLibraries] = useState([]);
  const [openedModal, setOpenedModal] = useState(false);
  const [selectedLesson, setSelectedLesson] = useState(null);
  const [checked, setChecked] = useState(false);
  const [time, setTime] = useState('');
  const close = () => setOpenedModal(false)
  const languages = ['SK', 'CZ', 'HU', 'PL'];
  const [selectedLanguage, setSelectedLanguage] = useState(languages[0]);
  const [language, setLanguage] = useState('SK');
  const [selectedTrack, setSelectedTrack] = useState(null);
  const [selectedLibrary, setSelectedLibrary] = useState(null);




  useEffect(() => {
    fetchLessons();
    fetchTracks();
    fetchLibraries();
    return () => {
      // Cleanup function if needed
      setLessons([]);
      setTracks([]);
      setLibraries([]);
      setOpenedModal(false);
      setSelectedLesson(null);
      setChecked(false);
      setTime('');
    }
  }, []);

  const fetchLessons = async () => {
    try {
      const response = await axios.get('/api/v1/lessons');
      setLessons(await response.data);
    } catch (error) {
      console.error('Error fetching lessons:', error);
    }
  };

  const fetchTracks = async () => {
    try {
      const response = await axios.get('/api/v1/tracks');
      setTracks(await response.data);
    } catch (error) {
      console.error('Error fetching tracks:', error);
    }
  };

  const fetchLibraries = async () => {
    try {
      const response = await axios.get('/api/v1/libraries');
      setLibraries(await response.data);
    } catch (error) {
      console.error('Error fetching libraries:', error);
    }
  };

  const getTableColumns = () => {
    return [{ accessor: 'time', title: 'Čas', textAlign: 'left' },
    { accessor: 'track', title: 'Zvuková stopa', textAlign: 'left' },
    {
      accessor: 'bell', title: 'Zvonček', render: (item) => (
        <Checkbox disabled checked={item}></Checkbox>
      )
    },
    { accessor: 'library', title: 'Knižnica', textAlign: 'left' }];
  };

  const getTableData = () => {
    var a = lessons.map(lesson => ({
      time: lesson.time,
      track: lesson.track.name,
      bell: lesson.bell,
      library: lesson.library.name
    }));
    return a;
  };

  const renderModal = () => {

    return (
      <Modal
        opened={openedModal}
        onClose={close}
        title='Detail hodiny'>
        <TimeInput mt="md" radius="lg" label="Čas" placeholder="Input component" value={selectedLesson ? selectedLesson.time : null}
          onChange={(event) => {
            setTime(event.currentTarget.value);
            selectedLesson.time = event.currentTarget.value;
            setSelectedLesson(selectedLesson);
          }} />
        <Select
          allowDeselect={false}
          label="Zvuková stopa"
          mt="md"
          radius="lg"
          placeholder="Vyberte zvukovú stopu"
          data={tracks.map(track => ({ value: track.name, label: track.name + ' ' + track.text }))}
          value={selectedTrack}
          onChange={(value) => {
            selectedLesson.track = value;
            setSelectedLesson(selectedLesson);
            setSelectedTrack(value);
          }}
        />
        <Checkbox label="Zvonček" mt="md" checked={checked}
          radius="lg"
          value={selectedLesson ? selectedLesson.bell : false}
          description="Zvonček sa spustí na začiatku zvoleného času"
          style={{ marginTop: '10px' }}
          onChange={(event) => {
            setChecked(
              event.currentTarget.checked);
            selectedLesson.bell = event.currentTarget.checked;
            setSelectedLesson(selectedLesson);
          }} />
        <Select
          allowDeselect={false}
          label="Knižnica"
          mt="md"
          radius="lg"
          placeholder="Vyberte knižnicu"
          data={libraries.map(library => ({ value: library.name, label: library.name }))}
          value={selectedLibrary}
          onChange={(value) => {
            selectedLesson.library = value;
            setSelectedLesson(selectedLesson);
            setSelectedLibrary(value);
          }}
        />
        <Group justify="center" mt="xl">
          <Button variant="filled" color="yellow" radius="md" onClick={() => onSaveLesson(selectedLesson)}>Uložiť</Button>
          <Button variant="filled" color="yellow" radius="md" onClick={() => onDeleteLesson(selectedLesson)}>Odstrániť</Button>
        </Group>
      </Modal>
    )
  };

  const handleRowClick = (lesson) => {

    setSelectedLesson(lesson);
    setChecked(lesson.bell);
    setTime(lesson.time);
    setSelectedTrack(lesson.track.name);
    setSelectedLibrary(lesson.library.name);
    setOpenedModal(true);
  };

  const onSaveLesson = (lesson) => {
    if (!lesson) {
      console.log('Saving lesson:', lesson);
      // Here you would typically send the updated lesson data to your backend API
      if (lesson.id) {
        // Update existing lesson
        axios.put(`/api/v1/lessons/${lesson.id}`, lesson)
          .then(response => {
            console.log('Lesson updated successfully:', response.data);
            fetchLessons(); // Refresh the lessons after updating
          })
          .catch(error => {
            console.error('Error updating lesson:', error);
          });
      } else {
        // Create new lesson
        axios.post('/api/v1/lessons', lesson)
          .then(response => {
            console.log('Lesson saved successfully:', response.data);
            fetchLessons(); // Refresh the lessons after saving
          })
          .catch(error => {
            console.error('Error saving lesson:', error);
          });
      };
    }
  };

  const onDeleteLesson = (lesson) => {
    if (!lesson) {
      console.log('Deleting lesson:', lesson);
      // Here you would typically send a delete request to your backend API
      axios.delete(`/api/v1/lessons/${lesson.id}`)
        .then(response => {
          console.log('Lesson deleted successfully:', response.data);
          fetchLessons(); // Refresh the lessons after deleting
        })
        .catch(error => {
          console.error('Error deleting lesson:', error);
        });
    }
  };



  return (
    <div className="App">
      <MantineProvider
        theme={{
          colorScheme: 'dark',
          colors: {
            // override dark colors here to change them for all components
            dark: [
              '#d5d7e0',
              '#acaebf',
              '#8c8fa3',
              '#666980',
              '#4d4f66',
              '#34354a',
              '#2b2c3d',
              '#1d1e30',
              '#0c0d21',
              '#01010a',
            ],
          },
        }}>
        <AppShell
          header={{ height: 60 }}
          padding="md">
          <AppShell.Header>
            <Container>
              <Group justify="space-between" style={{ width: '100%' }}>
                <Group justify="flex-start">
                  <Image src="/logo.png" alt="Logo" width={40} height={40}  href='https://google.sk'/>
                </Group>
                <Group justify='flex-end'>
                  <Select allowDeselect={false} value={selectedLanguage} data={languages}
                    onChange={(event) => setSelectedLanguage(event)} />
                </Group>
              </Group>
            </Container>
          </AppShell.Header>
          <AppShell.Main>
            <Container>
              <Group justify="flex-start">
                <Button variant="filled" color="yellow" radius="md" onClick={() => {
                  setSelectedLesson({ time: '', track: {}, bell: false, library: {} });
                  setOpenedModal(true);
                }}><i class="fa fa-home"></i>Pridať hodinu</Button>
              </Group>
              <SimpleGrid cols={{ base: 1, sm: 1 }} spacing="md" style={{ marginTop: '30px' }}>
                <DataTable
                  highlightOnHover
                  columns={getTableColumns()}
                  records={getTableData()}
                  onRowClick={({ record, index, event }) => {
                    handleRowClick(record);
                  }}></DataTable>
              </SimpleGrid>
            </Container>
          </AppShell.Main>
        </AppShell>
        {renderModal()}
      </MantineProvider>
    </div>
  );

};

export default App;


